@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap);
* {
  margin: 0
}

body {
  background-color: lightgray;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));  fading gradient */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  display: flex;
  height: 100vh;
  align-items: center;
  color: rgb(241, 177, 14);
  margin: 0 30px;
  font-family: 'Poppins', sans-serif;
}

.home-left {
  flex: 0.7 1;  
  border-right: 1px solid black;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-right {
  flex: 0.3 1;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: right;
}

.home-nav {
  text-decoration: none;
}

.home-option {
  color: black;
  font-size: 40px;
  padding: 20px 70px;
}

.home-intro {
  font-size: 50px;
  padding: 0 70px;
}

.home-option:hover {
  transition: 0.5s;
  color: rgb(241, 177, 14);
}

.home-resume {
  text-decoration: none;
  font-weight: bold;
}
.header {
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: 1px solid gray;
  font-family: 'Poppins', sans-serif;
}

.header-left {
  flex: 1 1;
}

.header-right {
  display: flex;
  justify-content: space-evenly;
}

.header-nav {
  text-decoration: none;
}

.header-options {
  padding: 0 20px;
  color: black;
  font-size: 20px;
}

.header-options:hover {
  transition: 0.5s;
  color: rgb(241, 177, 14);
}
.about {
  display: flex;
  padding: 50px 200px;
  font-family: 'Poppins', sans-serif;
}

.about-right {
  flex: 0.3 1;
  display: flex;
  justify-content: flex-end;
}

.about-left {
  flex: 0.7 1;
}

img {
  width: 400px;
  height: 400px;
}

.about-info {
  padding-right: 50px;
}
.project-image {
  display: block;
  max-width:400px;
  max-height:400px;
  width: 100%;
  height: 100%;
  min-width: 200px;
  border: 1px solid black;

}

.project-container:hover {
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  -webkit-transition: all 1s ease;
}

.project-container {
  filter: grayscale(0%);
  filter: gray;
  -webkit-filter: grayscale(0%);
  filter: none;
  transition: 1s ease;
}


.work-project {
  display: flex;
  flex-wrap: wrap;
}


.contact {
  display: flex;
  font-family: 'Poppins', sans-serif;
}

.contact-left {
  flex: 0.5 1;
  padding-top: 100px;
}

.contact-right {
  flex: 0.5 1;
  display: flex;
  justify-content: flex-end;
  height: 100vh;
}

.contact-title {
  margin-bottom: 20px;
  padding: 0 50px;
  margin-left: 50px;
  color: rgb(241, 177, 14);
}

.contact-info {
  margin-bottom: 20px;
  padding: 0 50px;
  margin-left: 50px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  padding: 0 50px;
  margin-left: 50px;
}

.contact-form > input, textarea {
  background-color: lightgray;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid gray;
  font-family: 'Courier New', Courier, monospace;
}

.contact-box {
  font-size: 17px;
}

button {
  padding: 10px;
  border: 1px solid gray;
  background-color: lightgray;
  font-family: 'Courier New', Courier, monospace;
}

button:hover {
  background-color: rgb(241, 177, 14);
  transition: 0.5s;
  opacity: 0.8;
}

.contact-map-box {
  display: flex;
  flex-direction: column;
}

.contact-soc {
  text-decoration: none;
}

